/******************************************************************
  - MIXINS -
******************************************************************/
.acf-block-preview .text-image {
  pointer-events: none;
}
.acf-block-preview .breadcrumbs {
  display: none;
}

.text-image .image_text-image {
  position: relative;
  height: 100%;
}
.text-image .image_text-image img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 50% 50%;
     object-position: 50% 50%;
  font-family: "object-fit";
}
.text-image .image_text-image video {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 50% 50%;
     object-position: 50% 50%;
  font-family: "object-fit";
}
.text-image .image_text-content {
  position: relative;
}
.text-image .image_text-content-background {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1;
}
.text-image .image_text-content-background img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 50% 50%;
     object-position: 50% 50%;
  font-family: "object-fit";
}
.text-image .image_text-content-text {
  padding: 80px 60px;
  z-index: 2;
  background: #767676;
  background: rgba(23, 121, 186, 0.9);
  position: relative;
}
.text-image .image_text-content-text h3, .text-image .image_text-content-text p {
  color: #fefefe;
}